* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
// breakpoints
$desktop-breakpoint: 768px;

// color pallette
$main-color: #595959;
$main-color-light: #7f7f7f;
$secondary-color: #a5a5a5;
$secondary-color-light: #cccccc;
$light-color: #f2f2f2;

body {
  background: $main-color;
  color: $light-color;
}


p {
  font-family: monospace;
  font-size: 1.3rem;
}

#root {
  .App {
    .cardContainer {
      padding: 0 1.5em;
      max-width: 500px;
      min-width: 450px;
      margin: 0 auto;
      .card {
        display: flex;
        margin: 1em 0;
        background: $secondary-color;
        img {
          width: 30%;
        }
        .card-text {
          padding: .5em;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
        }
      }
    }
  }
}

@media(min-width: $desktop-breakpoint) {
  #root {
    .App {
      .cardContainer {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        max-width: 1245px;
        margin: 0 auto;
        .card {
          margin: .5em;
          img {

          }
          .card-text {

          }
        }
      }
    }
  }
}